#booking-fixed.fixed {
  position: fixed !important;
  top: 7%;
  left: 0;
  width: 100%;
  width: calc(100% + 10px);
  z-index: 1;
  transition: all 0.3s;
  background-color: white;
}



.ant-picker {
  min-height: 39px;
  border-radius: 5px !important;
}

.ant-picker,
input,
