header.sticky {
  position: fixed !important;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  transition: all 0.3s;
}
nav {
  background-color: #333;
  z-index: 1000;
  padding: 2rem 0;
}
nav .title-app {
  color: #fff !important;
}

nav a {
  color: white;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}
nav ul {
  margin: 0 !important;
}
nav ul li {
  display: inline-block;
  padding: 10px 20px;
  opacity: 0.7;
}
nav ul li a:hover {
  opacity: 1;
  color: #fff !important;
}

nav ul li img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 5px;
}

/* --------- Dropdown ---------- */
.drop-nav {
  position: relative;
  padding: 10px 0;
}
.drop-nav ul {
  padding-left: 0 !important;
}
.drop-nav a {
  text-transform: capitalize;
  overflow: hidden;
}
.drop-nav a:hover {
  color: white;
}
.drop-nav .dropdown {
  position: absolute;
  background: #282c34;
  width: 100%;
  width: calc(100% + 50px);
  top: 50px;
  display: none;
}
.drop-nav .dropdown li {
  display: block;
}
.drop-nav:hover .dropdown {
  display: block;
  text-align: center;
}

